.loadingPage {
    height: 90vh;
    width: 100%;
    display: grid;
    place-items: center;
    
  background-color: #ef8e0e;
  }
  
  .loadingCircle {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 10px solid #340bed;
    border-top: 10px solid #0eef19;
    border-bottom: 10px solid #0eef16;
    animation: loading 1s linear infinite;
  }
  
  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }