.newPost{
    min-height:100vh;
    background-image:linear-gradient(to right,#dabe97 , #ef8e0e);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2vmax;
    box-sizing: border-box;
}

.newPostForm{
    background-color: #05cffc;
    height: 100%;
    width: 50%;
    border-radius:30px;
    box-sizing: border-box;
    padding: 2vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.newPostForm > img {
    width: 100%;
    object-fit: cover;
}

.newPostForm > h3 {
    margin:2vmax;
}

.newPostForm > input[type="text"]{
    width: 100%;
    padding: 1vmax;
    border-radius: 30px;
    border: none;
    outline: none;
    font: 100 1.2rem "Roboto",sans-serif;
}

.newPostForm > input[type="file"]{
    border-radius: 30px;
    margin: 2vmax;
}



.newPostForm > input[type="file"]::-webkit-file-upload-button{
    background-color: rgb(221, 118, 15);
    color: #060401;
    width: 100%;
    padding: 1vmax;
    border-radius: 30px;
    border: none;
    font: 100 1.2rem "Roboto",sans-serif;
}


@media screen and (max-width:600px){
    
    .newPostForm{
        padding:4vw 8vw;
        width:100%;
    }

    .newPostForm > input {
        padding: 4vw 8vw;
        margin: 2vmax;
        font: 100 1.2rem "Roboto",sans-serif;
        margin: 4vw;
    }

    .newPostForm > input[type="file"]{
        width: 100%;
    }

    .newPostForm > input[type="file"]::-webkit-file-upload-button{
        width: 70%;
    }
}
